import { Navbar } from "./Components/Navbar";
import { ToDolist } from "./Components/ToDolist";
import React, { useState, useEffect } from "react";
import { AddTodo } from "./Components/AddTodo";
import { Footer } from "./Components/Footer";
import { About } from "./Components/About";
import { BrowserRouter as Router, Switch, Route} from "react-router-dom";

function App() {
  let initTodo;
  if (localStorage.getItem("todos") == null) {
    initTodo = [];
  } else {
    initTodo = JSON.parse(localStorage.getItem("todos"));
  }

  const addItem = (todoitem) => {
    setTodos([...todos, { Sno: todos.length + 1, Todo: todoitem }]);
  };

  const Ondelete = (todoitem) => {
    console.log(todoitem);

    let todoelements = todos.filter((e) => {
      return e.Todo !== todoitem;
    });

    setTodos(todoelements);
  };

  const [todos, setTodos] = useState(initTodo);

  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos]);

  return (
    <>
      <Router>
        <Navbar Title="TodoList" />
        <Switch>
          <Route
            exact
            path="/"
            render={() => {
              return (
                <>
                  <AddTodo todos={todos} Additem={addItem} />
                  <ToDolist todos={todos} Ondelete={Ondelete} />
                </>
              );
            }}
          ></Route>

          <Route exact path="/about">
            <About />
          </Route>
        </Switch>

        <Footer />
      </Router>
    </>
  );
}

export default App;
