import React from 'react'

export const Footer = () => {
    let footerstyle={
        position: "fixed",
  left: "0",
  bottom: "0",
  width: "100%"
    }
    return (
       
       <>
            <footer className="bg-dark text-light py-3 sticky-bottom"  style={footerstyle}>
            <p className="text-center">
            Copyright &copy; MyTodosList.com
            </p>
            </footer>
       </>
    )
}
